/**
 * External dependencies
 */
import camelCase from 'lodash/camelCase';
import kebabCase from 'lodash/kebabCase';
import mapValues from 'lodash/mapValues';
import uniq from 'lodash/uniq';
import { calculate as getSelectorPriority } from 'specificity';
import type {
	CssEditableProp,
	CssLeafPropName,
	CssProp,
	CssPropName,
	CssShorthandPropName,
	Maybe,
} from '@nab/types';

export function expandProp< T extends CssProp >( prop: T ): ReadonlyArray< T > {
	if ( ! isShorthandProp( prop.name ) ) {
		return [ prop ];
	}

	const el = document.createElement( 'div' );
	el.style[ prop.name ] = prop.value;
	const el2 = document.createElement( 'div' );
	el2.style[ prop.name ] =
		'readonly' !== prop.type ? prop.base?.value ?? '' : '';
	return expandPropName( prop.name ).map(
		( leafName ): T => ( {
			...prop,
			name: leafName,
			value: el.style[ leafName ],
			...( 'readonly' !== prop.type &&
				prop.base && {
					base: {
						...prop.base,
						value: el2.style[ leafName ],
					},
				} ),
		} )
	);
}

export function expandPropName(
	propName: string
): ReadonlyArray< CssLeafPropName > {
	propName = camelCase( propName );
	return isPropName( propName ) ? EXPANSIONS[ propName ] : [];
}

export function getGlobalProps(
	selector: string,
	el: HTMLElement,
	styleTag: HTMLElement | null
): ReadonlyArray< CssProp > {
	const css = styleTag?.innerHTML ?? '';
	if ( styleTag ) {
		styleTag.innerHTML = '';
	}

	const style = getComputedStyle( el );
	const priority = getSelectorPriority( selector );
	const props = Object.keys( TEST_PROP_VALUES )
		.map( ( leafName ) => leafName as CssLeafPropName )
		.map( ( name ): CssEditableProp & { name: CssLeafPropName } => ( {
			type: 'regular',
			name,
			selectors: [ '.nelio-global-prop', selector ],
			value: style[ name ],
			base: undefined,
			ruleIndex: -1,
			priority,
		} ) );

	if ( ! styleTag ) {
		return props;
	}

	const higherPriority = { ...priority, A: priority.A + 1 };
	styleTag.innerHTML = '';

	const computedStyles = mapValues( TEST_PROPS_CSS, ( _, i ) => {
		const k = i as keyof typeof TEST_PROPS_CSS;
		const cssStyle = TEST_PROPS_CSS[ k ];
		const importantSelector = increaseSelectorImportance( selector );
		styleTag.innerHTML =
			'defaults' === k
				? `${ importantSelector } { ${ cssStyle } }`
				: `${ selector } { ${ cssStyle } }`;
		const currentStyle = getComputedStyle( el );
		return LEAF_PROP_NAMES.reduce(
			( r, n ) => ( { ...r, [ n ]: currentStyle[ n ] } ),
			{} as unknown as Record< CssLeafPropName, string >
		);
	} );

	const result = props
		.map( ( p ) => ( {
			...p,
			defaultValue: computedStyles.defaults[ p.name ],
		} ) )
		.map( ( p ): CssProp & { defaultValue: string } => {
			const val1 = computedStyles.base[ p.name ];
			const val2 = computedStyles.alternative[ p.name ];
			if ( val1 !== val2 ) {
				return p;
			}

			const impVal1 = computedStyles.importantBase[ p.name ];
			const impVal2 = computedStyles.importantAlternative[ p.name ];
			if ( impVal1 !== impVal2 ) {
				return { ...p, priority: higherPriority, type: 'important' };
			}

			return { ...p, priority: higherPriority, type: 'readonly' };
		} );

	styleTag.innerHTML = css;

	return result
		.filter( ( p ) => 'regular' !== p.type || p.value !== p.defaultValue )
		.map( fixNumericValues );
}

export const isPropName = ( name: string ): name is CssPropName =>
	( PROP_NAMES as string[] ).includes( name );

export function getPreviewValue( prop: Maybe< CssProp > ): string {
	return getUserValue( prop ) || getInheritedValue( prop ) || '';
}

export function getUserValue( prop: Maybe< CssProp > ): Maybe< string > {
	return 'readonly' === prop?.type ? undefined : prop?.value;
}

export function getInheritedValue( prop: Maybe< CssProp > ): Maybe< string > {
	return 'readonly' === prop?.type ? prop.value : prop?.base?.value;
}

export function hasUserValue( prop: Maybe< CssProp > ): boolean {
	return (
		!! getUserValue( prop ) &&
		getUserValue( prop ) !== getInheritedValue( prop )
	);
}

export function hasInheritedValue( prop: Maybe< CssProp > ): boolean {
	return !! getInheritedValue( prop );
}

export function byName< T extends { readonly name: CssPropName } >(
	props: ReadonlyArray< T >
): Partial< Record< CssPropName, T > > {
	return props.reduce(
		( r, p ) => ( { ...r, [ p.name ]: p } ),
		{} as Partial< Record< CssPropName, T > >
	);
}

// =======
// HELPERS
// =======

const isShorthandProp = ( p: string ) => ! isLeafPropName( p );

const isLeafPropName = ( p: string ): p is CssLeafPropName =>
	( LEAF_PROP_NAMES as string[] ).includes( p );

const increaseSelectorImportance = ( selectors: string ): string =>
	selectors
		.split( ',' )
		.map( ( selector ) => {
			const parts = selector.trim().split( ' ' );
			for ( let i = 0; i < 8; ++i ) {
				for ( let j = 0; j < parts.length; ++j ) {
					parts[ j ] += parts[ j ] ?? '';
				}
			}
			return parts.join( ' ' );
		} )
		.join( ', ' );

const fixNumericValues = ( p: CssProp ): CssProp => {
	const value = fixNumericValue( p.value );
	if ( 'readonly' === p.type || ! p.base ) {
		return { ...p, value };
	}

	const baseValue = p.base.value;
	return { ...p, value, base: { ...p.base, value: baseValue } };
};

const fixNumericValue = ( input: string ): string => {
	const [ _, value, units ] = input.match( /^(\d+\.\d{3,})([a-z%]+)$/ ) ?? [];
	if ( ! value || ! units ) {
		return input;
	}

	const newValue = `${ Math.round( parseFloat( value ) ) }`;
	return `${ newValue }${ units }`;
};

