const INTERMEDIATE_PROP_NAMES = Object.keys( SHORTHAND_MAP )
	.flatMap( ( n ) => SHORTHAND_MAP[ n as keyof typeof SHORTHAND_MAP ] )
	.filter( ( n ) => n in SHORTHAND_MAP )
	.map( ( n ) => n as CssPropName );
const ROOT_PROP_NAMES = Object.keys( SHORTHAND_MAP )
	.map( ( n ) => n as keyof typeof SHORTHAND_MAP )
	.filter( ( n ) => n !== '_extra' )
	.map( ( n ) => n as CssPropName )
	.filter( ( n ) => ! INTERMEDIATE_PROP_NAMES.includes( n ) );
const LEAF_PROP_NAMES = Object.keys( SHORTHAND_MAP )
	.flatMap( ( n ) => SHORTHAND_MAP[ n as keyof typeof SHORTHAND_MAP ] )
	.filter( ( n ) => ! ( n in SHORTHAND_MAP ) )
	.map( ( n ) => n as CssPropName );

const PROP_NAMES: ReadonlyArray< CssPropName > = [
	...ROOT_PROP_NAMES,
	...INTERMEDIATE_PROP_NAMES,
	...LEAF_PROP_NAMES,
];

function expand( n: CssPropName ): ReadonlyArray< CssLeafPropName > {
	return isLeafPropName( n ) ? [ n ] : SHORTHAND_MAP[ n ].flatMap( expand );
}
const EXPANSIONS = PROP_NAMES.reduce(
	( r, n ) => ( { ...r, [ n ]: uniq( expand( n ) ) } ),
	{} as Record< CssPropName, ReadonlyArray< CssLeafPropName > >
);

