const strintifyTestProps = ( i: 0 | 1, imp: boolean ) =>
	Object.keys( TEST_PROP_VALUES )
		.map( ( k ) => k as CssLeafPropName )
		.map( ( k ) => [ kebabCase( k ), TEST_PROP_VALUES[ k ][ i ] ] as const )
		.map( ( [ k, v ] ) => `${ k }: ${ v } ${ imp ? '!important' : '' };` )
		.join( '\n' );

const TEST_PROPS_CSS = {
	base: strintifyTestProps( 0, false ),
	importantBase: strintifyTestProps( 0, true ),
	alternative: strintifyTestProps( 1, false ),
	importantAlternative: strintifyTestProps( 1, true ),
	defaults: Object.keys( TEST_PROP_VALUES )
		.map( ( k ) => `${ kebabCase( k ) }: revert !important;` )
		.join( '\n' ),
};
